<template>
  <div class="page page-sale">
    <div class="top-wrapper">
      <div class="merchant">
        <div class="left">
          <van-image
            :src="$root.getImageUrl($root.userInfo.avatar)"
            class="avatar"
            fit="cover"
            round
          />
          <div class="seller-info">
            <div class="name">{{ $root.userInfo.nickname }}</div>
            <Level :level="this.$root.userInfo.creditLevel" />
          </div>
        </div>
        <div class="right">
          <div class="label">可出售总额（V币）</div>
          <div class="amount">
            {{ parseFloat(balance).toFixed(2) }}
          </div>
        </div>
      </div>
    </div>
    <van-form @submit="handlerSubmit">
      <van-field
        v-model="form.amount"
        autocomplete="new-password"
        label="出售数量："
        :placeholder="`最低出售数量为${minimumSellAmount}V币`"
        type="number"
      />
      <van-field
        v-if="form.isSplit"
        v-model="form.minBuyEach"
        autocomplete="new-password"
        label="最低购买："
        :placeholder="minimumBuyAmountPlaceholder"
        type="number"
      />
      <van-collapse v-model="form.paymentType">
        <van-checkbox-group v-model="form.paymentType">
          <van-cell-group title="请选择收款方式">
            <template v-for="(v, k) in localPayments">
              <van-collapse-item
                v-if="
                  k === 'ali' || k === 'wechat'
                    ? showAlipayWechatForMerchantWhenItsOn
                    : k !== 'usdt'
                "
                :key="k"
                :disabled="v.list.length === 0"
                :name="v.type"
                :title="v.list.length === 0 ? '未绑定该收款方式' : v.typeName"
                class="payment"
                clickable
              >
                <template #icon>
                  <div :class="k" class="payment-icon"></div>
                </template>
                <template #right-icon>
                  <van-checkbox :disabled="v.list.length === 0" :name="v.type" shape="square" />
                </template>

                <van-radio-group v-model="form.payments[k]">
                  <template v-for="(item, index) in v.list">
                    <template v-if="v.type === PAYMENT_METHOD.BANK">
                      <van-cell
                        :key="index"
                        :title="$root.banksIdMappingName[item.bankId]"
                        :value="item.bankNo | safeBankNumber"
                        class="paymentChannel"
                        clickable
                        @click="form.payments[k] = item.id"
                      >
                        <template #right-icon>
                          <van-radio :name="item.id" />
                        </template>
                      </van-cell>
                    </template>
                    <template v-else>
                      <VirtualPayment
                        :key="index"
                        :type="k"
                        v-bind="item"
                        @click="form.payments[k] = item.id"
                      >
                        <van-radio :name="item.id" style="margin-left: 15px" />
                      </VirtualPayment>
                    </template>
                  </template>
                </van-radio-group>
              </van-collapse-item>
            </template>
          </van-cell-group>
        </van-checkbox-group>
      </van-collapse>

      <van-cell-group :border="false" class="split" title="是否拆分">
        <van-radio-group v-model="form.isSplit">
          <van-radio v-if="!hideSeparately" :name="1" class="btn">
            <div slot="icon" class="btn">可拆分</div>
          </van-radio>
          <van-radio :name="0" class="btn">
            <div slot="icon" class="btn">不可拆分</div>
          </van-radio>
        </van-radio-group>
      </van-cell-group>

      <van-button class="block primary" native-type="submit"> 发布挂单 </van-button>
    </van-form>
  </div>
</template>

<script>
import { PAYMENT_METHOD } from "@/vendor/enum";
import { createSaleOrder } from "@/apis/market";
import { getBusinessConfig } from "@/apis/crypto";
import Level from "@/components/Level.vue";
import VirtualPayment from "@/components/business/VirtualPayment.vue";
import { verifyGoogleAuth, verifySecuretedCode } from "@/vendor/globalVerify";
import { cryptoMerchantMixins, cryptoPaymentsMixins } from "@/vendor/mixins";

export default {
  components: { Level, VirtualPayment },
  mixins: [cryptoMerchantMixins, cryptoPaymentsMixins],
  data() {
    return {
      PAYMENT_METHOD,
      collapse: "",
      form: {
        amount: "",
        minBuyEach: "",
        paymentType: [],
        isSplit: 1,
        payments: {},
      },
      sellingConfig: {
        wechatAlipayAmountMin: {
          min: 10,
          max: 10000,
        },
        wechatAlipayAmountMiddle: {
          min: 10001,
          max: 30000,
        },
        wechatAlipayAmountMax: {
          min: 30001,
          max: 60000,
        },
        bankCardAmountMin: {
          min: 33,
          max: 10000,
        },
        bankCardAmountMiddle: {
          min: 10003,
          max: 30000,
        },
        bankCardAmountMax: {
          min: 30001,
          max: 60000,
        },
      },
    };
  },
  computed: {
    showAlipayWechatForMerchantWhenItsOn() {
      if (this.isMerchant) {
        return this.$root.isMerchantAlipayWechatOpen;
      } else {
        return true;
      }
    },
    localPayments() {
      const obj = {};
      for (const key in this.payments) {
        if (this.hideAlipayWechat && (key === "ali" || key === "wechat")) {
          // 当前为隐藏支付宝微信支付方式
        } else {
          obj[key] = this.payments[key];
        }
      }
      return obj;
    },
    paymentsMappingIds() {
      const map = {};
      for (const key in this.payments) {
        const ele = this.payments[key];
        map[ele.type] = ele.list;
      }
      return map;
    },
    balance() {
      return this.isCryptoMerchant
        ? this.$root.cryptoMerchantInfo.businessBalance
        : this.$root.userInfo.validSellBalance;
    },
    matchPaymentConfig() {
      const { wechatAlipayAmountMin, bankCardAmountMin } = this.sellingConfig;
      const list = [];
      let isContainWechatAlipay = false;
      let isContainBank = false;
      (this.form.paymentType || []).forEach((payment) => {
        if (payment === PAYMENT_METHOD.WE_CHAT || payment === PAYMENT_METHOD.ALI) {
          isContainWechatAlipay = true;
        }
        if (payment === PAYMENT_METHOD.BANK) {
          isContainBank = true;
        }
      });

      if (isContainWechatAlipay) list.push(wechatAlipayAmountMin);
      if (isContainBank) list.push(bankCardAmountMin);

      let matchedConfig = null;

      list.forEach((config) => {
        if (matchedConfig === null) matchedConfig = config;
        if (config.min > matchedConfig.min) matchedConfig = config;
      });

      return matchedConfig;
    },
    hideSeparately() {
      if (this.form.amount && this.matchPaymentConfig) {
        if (this.form.amount <= this.matchPaymentConfig.min * 2) {
          this.form.isSplit = 0;
          return true;
        }
      }
      return false;
    },
    minimumSellAmount() {
      if (!this.matchPaymentConfig) return 10;
      return this.isCryptoMerchant ? this.matchPaymentConfig.min : 10;
    },
    minimumBuyAmount() {
      if (!this.matchPaymentConfig) return 0;
      return this.isCryptoMerchant ? this.matchPaymentConfig.min : 0;
    },
    minimumBuyAmountPlaceholder() {
      if (this.minimumBuyAmount) {
        return `最低购买数量为${this.minimumBuyAmount}V币`;
      } else {
        return `请填写买家最低可购买数量`;
      }
    },
  },
  watch: {
    "form.paymentType"(newVal, oldVal) {
      const set = new Set(oldVal);
      for (const v of newVal) {
        // 新增
        if (!set.has(v)) {
          this.form.payments[
            v === PAYMENT_METHOD.BANK ? "bank" : v === PAYMENT_METHOD.ALI ? "ali" : "wechat"
          ] = this.paymentsMappingIds[v][0].id;
          break;
        }
      }

      const setN = new Set(newVal);
      for (const v of oldVal) {
        if (!setN.has(v)) {
          this.form.payments[
            v === PAYMENT_METHOD.BANK ? "bank" : v === PAYMENT_METHOD.ALI ? "ali" : "wechat"
          ] = "";
          break;
        }
      }
    },
    "form.isSplit"() {
      this.form.minBuyEach = "";
    },
  },
  created() {
    if (this.isCryptoMerchant) this.getBusinessConfig();
    this.$root.loadPayments();
    this.loadPayments();
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
  },
  methods: {
    loadPayments() {
      const bindData = () => {
        Object.keys(this.payments).forEach((key) => {
          this.$set(this.form.payments, key, "");
        });
      };
      if (this.$root.isBindAnyPayment) bindData();
      else this.$root.loadPayments().then(({ ok }) => ok && bindData());
    },
    async getBusinessConfig() {
      const { ok, data, msg } = await getBusinessConfig();
      if (ok) {
        this.sellingConfig = data.amount_range_config;
      } else {
        this.$toast(msg);
      }
    },
    async handlerSubmit() {
      const payment = Object.entries(this.form.payments)
        .map(([_, v]) => v)
        .filter((v) => v > 0)
        .join(",")
        .toString();

      if (this.form.amount < 10) return this.$toast.fail("最低出售量为10");
      if (this.form.amount > Number(this.balance))
        return this.$toast.fail("出售量不得高于可售数量");
      if (!payment.length) return this.$toast.fail("请选择支付方式");
      let paymentVerifyFlag = true;
      this.form.paymentType.forEach((payment) => {
        if (payment === PAYMENT_METHOD.BANK && this.payments.bank.list.length === 0)
          return (this.paymentVerifyFlag = false);
        if (payment === PAYMENT_METHOD.ALI && this.payments.ali.list.length === 0)
          return (this.paymentVerifyFlag = false);
        if (payment === PAYMENT_METHOD.WE_CHAT && this.payments.wechat.list.length === 0)
          return (this.paymentVerifyFlag = false);
      });
      if (!paymentVerifyFlag) return this.$toast.fail("请选择支付账户");
      if (this.form.isSplit && !this.form.minBuyEach) return this.$toast.fail("请输入最低购买数量");
      if (this.form.isSplit && this.form.minBuyEach.toNumber() > this.form.amount.toNumber())
        return this.$toast.fail("最低购买量不得高于出售量");
      // 当前用户为商家，验证出售金额与最小可购买金额
      if (this.isCryptoMerchant && this.form.amount < this.minimumBuyAmount) {
        return this.$toast.fail(`最低出售数量为${this.minimumBuyAmount}V币`);
      }
      // 当前用户为商家，验证出售金额与最小可出售金额
      if (this.isCryptoMerchant && this.form.amount < this.minimumSellAmount) {
        return this.$toast.fail(`最低出售数量为${this.minimumSellAmount}V币`);
      }

      const securePassword = await verifySecuretedCode(this);
      if (securePassword && securePassword.length === 6) {
        const isPassed = await verifyGoogleAuth(this);
        if (!isPassed) return;
        const { ok, msg } = await createSaleOrder(
          this.$tls.processParams(
            {
              amount: Number(this.form.amount),
              minBuyEach: Number(this.form.minBuyEach),
              paymentType: this.form.paymentType
                .reduce((previous, current) => Number(previous) + Number(current), 0)
                .toString(),
              paymentTypeId: payment,
              isSplit: this.form.isSplit.toString(),
              maxBuyEach: Number(this.form.amount),
              sign: "",
              securePassword,
            },
            ["float", "shake"]
          ),
          this.ApiPath
        );
        if (ok) {
          this.$root.fetchUserInfo();
          if (this.isCryptoMerchant) {
            return await this.$router.back(-1);
          }
          await this.$router.push("my_hang_orders");
        } else {
          this.$toast.fail(msg);
        }
      }
    },
  },
};
</script>

