            height: 22.5px;
            line-height: 22.5px;
          }
        }
      }

      .right {
        color: #333;

        .label {
          font-size: 12px;
        }

        .amount {
          font-size: 18px;
          margin-top: 4px;
        }
      }
    }
  }

  .payment-icon {
    margin-right: 12px;

    &.bank {
      width: 35px;
